import React from 'react';
import {Link} from 'react-router';

const Navbar = (props) => {
  return (
	<div>
		<nav className="navbar navbar-inverse navbar-static-top" role="navigation">
    <div className="container">
							<div className="navbar-header">
								<button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar"
									aria-expanded={props.aria} aria-controls="navbar" onClick={() => {
										props.expandNav();
									}}>
									<span className="sr-only">Toggle navigation</span>
									<span className="icon-bar"></span>
									<span className="icon-bar"></span>
									<span className="icon-bar"></span>
								</button>
							</div>

        <div id='navbar' className="navbar-collapse collapse in" aria-expanded={props.aria}>
            <ul className="nav navbar-nav">
                <li><a href="/">Home</a></li>
                <li><a href="/">About</a></li>
								{/* { !props.auth ? (
									<li><Link href="/login">Login/SignUp</Link></li>
								) : (
									<li><Link onClick={() => {props.loggingOut()}}>Logout</Link></li>
								)
								} */}
            </ul>
        </div>
    </div>
		</nav>
	</div>
  )
}
export default Navbar;



